body {
  background: #f9f9f9;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1 {
  text-align: center;
  padding: 1rem;
}

form {
  flex-wrap: wrap;
  justify-content: center;
  padding: 1rem;
  display: flex;
}

.form-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  min-height: 230px;
  margin-top: 2rem;
  padding: 0 1rem;
  display: flex;
}

.form-card {
  background: #fff;
  border-radius: 6px;
  max-width: 300px;
  padding: .5rem 1.5rem;
  box-shadow: 0 2px 8px #0000000f;
}

.form-card h2 {
  text-align: center;
  color: #333;
  margin-bottom: 1rem;
  font-size: 1.25rem;
}

.form-card label {
  color: #444;
  margin-bottom: .25rem;
  font-size: .9rem;
  font-weight: 600;
  display: block;
}

.form-card input, .form-card select {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: .75rem;
  padding: .45rem .75rem;
  font-size: .95rem;
}

.form-card input:focus, .form-card select:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 0 2px #007bff1a;
}

.form-card button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 0 auto;
  padding: .4rem 1.25rem;
  font-size: .95rem;
  transition: background .2s;
  display: block;
}

.form-card button:hover {
  background-color: #0056b3;
}

.toast.success {
  background-color: #28a745;
}

.toast.error {
  background-color: #dc3545;
}

@keyframes fadein {
  from {
    opacity: 0;
    transform: translateX(-50%)translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateX(-50%)translateY(0);
  }
}

select, input[type="text"], button {
  padding: .5rem;
  font-size: 1rem;
}

.task-board {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 1.5rem;
  padding: 2rem 1rem;
  display: flex;
}

.task-column {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: .5rem;
  width: 100%;
  max-width: 300px;
  min-height: 100px;
  padding: 1rem;
  box-shadow: 0 2px 6px #0000000d;
}

.task-column h2 {
  text-align: center;
  margin-top: 0;
}

.task-card {
  background: #f1f1f1;
  border-radius: .5rem;
  margin-bottom: 1rem;
  padding: .75rem;
  transition: background .2s, box-shadow .2s;
  position: relative;
  box-shadow: 0 1px 4px #0000001a;
}

.task-card:hover {
  cursor: pointer;
  background: #e9e9e9;
  box-shadow: 0 2px 8px #0000001f;
}

.task-button-group {
  justify-content: center;
  margin-top: .75rem;
  display: flex;
}

.task-delete-icon {
  cursor: pointer;
  color: #999;
  font-size: 1rem;
  transition: color .2s, transform .2s;
  display: none;
  position: absolute;
  top: .5rem;
  right: .5rem;
}

.task-card:hover .task-delete-icon {
  display: inline;
}

.task-delete-icon:hover {
  transform: scale(1.15);
}

.task-card h3 {
  margin: 0 0 .5rem;
}

.task-card button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: .25rem;
  margin-top: .5rem;
  padding: .4rem .8rem;
}

.task-card button:hover {
  background-color: #0056b3;
}

.task-reset-icon {
  cursor: pointer;
  font-size: 1.3rem;
  transition: color .2s, transform .2s;
  display: none;
  position: absolute;
  top: .5rem;
  right: .5rem;
}

.task-card:hover .task-reset-icon {
  display: inline;
}

.task-reset-icon:hover {
  transform: scale(1.15);
}

.task-undo-icon {
  cursor: pointer;
  font-size: 1.3rem;
  transition: color .2s, transform .2s;
  display: none;
  position: absolute;
  top: .5rem;
  right: .5rem;
}

.task-card:hover .task-undo-icon {
  display: inline;
}

.task-undo-icon:hover {
  transform: scale(1.15);
}

.task-card select {
  box-sizing: border-box;
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-top: .5rem;
  padding: .45rem .75rem;
  font-family: inherit;
  font-size: .95rem;
}

.task-card select:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 0 2px #007bff1a;
}

.task-card option {
  color: #333;
  background-color: #fff;
  font-weight: normal;
}

